import { dynamicApp } from '../dynamics';
export var VxeTextarea = Object.assign(VxeTextareaComponent, {
    install: function (app) {
        app.component(VxeTextareaComponent.name, VxeTextareaComponent);
    }
});
export var Textarea = VxeTextarea;
